/* index.css */
@import url('fonts.css');

body {
  margin: 0;
  font-family: 'Cutive Mono', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Cutive Mono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}
